import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { deviceType, osName } from "react-device-detect"
import { asyncWithLDProvider} from "launchdarkly-react-client-sdk";
import getUserId from "./util/getUserId";
import 'bootstrap/dist/css/bootstrap.css';


//const DEMOKEY = "Demo-User-Multiscreen4";

// This key will be used in demo environment to ensure the experiment shows the "right" value for that user
const DEMOKEY = process.env.REACT_APP_DEMOKEY;

// The LaunchDarkly Client key which will be used
const CLIENTKEY =  process.env.REACT_APP_CLIENTKEY;
console.log("clien key is ",CLIENTKEY)
// Roboto const DEMOKEY = "Demo-User-Multiscreen4";
// Phone context const DEMOKEY = "Demo-User-Multiscreen5";
// Chat const DEMOKEY = "Demo-User-Multiscreen6";
//const DEMOKEY = "Demo-User-Multiscreen5";


const root = ReactDOM.createRoot(document.getElementById('root'));
let id = getUserId();


// We will just assume that any mac laptop is running the demo
if (deviceType === "browser" && osName === "Mac OS") {
  id = DEMOKEY;
} 

(async () => {
  const LDProvider = await asyncWithLDProvider({
    clientSideID: CLIENTKEY,
    timeout: 1,
    user: {
      key: id,
      //dynamically set these custom attributes using the deviceType and osName selectors from the npm package
      custom: {
        device: deviceType,
        operatingSystem: osName,
        state: "unknown",
      },
    },
  });
  

  // at this point if the key is DEMOKEY change it to the key supplied with the FF


  root.render(
    <LDProvider>
      <App />

    </LDProvider>
  );
})();
// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
